// 1. íšŒì›ê°€ì… (POST /user/signup)
export async function signup(userDto: UserDTO) {
  const url = `${USERSERVICE_API}/signup`;

  const response = await fetch(url, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(userDto),
  });

  const text = await response.text();

  if (response.status === 201) {
    return { success: true, message: text || "íšŒì›ê°€ì…ì´ ì™„ë£Œë˜ì—ˆìŠµë‹ˆë‹¤." };
  }

  // ëª¨ë“  ì—ëŸ¬ë„ ë©”ì‹œì§€ ê·¸ëŒ€ë¡œ ë°˜í™˜
  return { success: false, message: text || "íšŒì›ê°€ì… ì¤‘ ë¬¸ì œê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤." };
}

// 2. ë¡œê·¸ì¸ (POST /user/login)
export async function login(loginDto: LoginDTO): Promise<LoginResponse> {
  const url = `${USERSERVICE_API}/login`;

  const response = await fetch(url, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(loginDto),
  });

  if (response.ok) { // 200 OK
    // Authorization í—¤ë”ì—ì„œ Bearer í† í° ì¶”ì¶œ
    const authHeader = response.headers.get("Authorization");
    const accessToken = authHeader?.startsWith("Bearer ") ? authHeader.substring(7) : "";

    // ì‘ë‹µ ë³¸ë¬¸ íŒŒì‹± (Map<String, String> -> JS Object)
    const body: { [key: string]: string } = await response.json();

    return {
      userSignId: body["userSignId"],
      role: body["role"],
      refreshToken: body["refreshToken"],
      accessToken: accessToken,
    };
  } else {
    throw new Error(`ë¡œê·¸ì¸ ì‹¤íŒ¨: HTTP ${response.status}`);
  }
}

